.empty-p {
    position: relative;
    text-align: center;
    font-size: 20px;
    font-weight: 400;
    color: coral;
    animation: move 2s ease-in infinite;
}

@keyframes move {
    0%{
        left:0;
    }
    50%{
        right: 12px;
    }
    100%{
        left: 12px;
    }
}